.smart-image-wrapper {
  position: relative;
  overflow: hidden;
  background-color: #e1e1e1;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.smart-image-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}
.smart-image-spinner {
  width: 36px;
  height: 36px;
  border: 4px solid rgba(0, 0, 0, 0.15);
  border-top-color: #331C5B;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@keyframes jump {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
.smart-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}
.smart-image.loaded {
  opacity: 1;
}
.smart-image.fallback {
  object-fit: contain;
}
.smart-image-error {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #e00;
  font-weight: 400;
  text-align: center;
  font-size: 14px;
}
.next-icon {
  cursor: pointer;
}
.next-icon.prev {
  transform: rotate(180deg);
}
.next-icon.disabled {
  opacity: 0;
}
.book-viewer {
  display: flex;
  align-items: center;
